#sidebar {
  position: fixed;
  top:0px;
  left:0px;
  display: block;
  float: left;
  width: 20%;
  height: 120vh;
  background-color: #57068C;
  color: #ffffff;
  text-align: center;
  z-index: 10
}

#user {
  padding-top: 30px;
  padding: 30px;
  font-size: 30px;
}

.in {
  display: inline-block;
  -webkit-transform: rotate( 180deg );
          transform: rotate( 180deg );
  -webkit-transition: -webkit-transform 150ms ease;
  transition: -webkit-transform 150ms ease;
  transition: transform 150ms ease;
  transition: transform 150ms ease, -webkit-transform 150ms ease;
}

.out {
  display: inline-block;
  -webkit-transition: -webkit-transform 150ms ease;
  transition: -webkit-transform 150ms ease;
  transition: transform 150ms ease;
  transition: transform 150ms ease, -webkit-transform 150ms ease;
}

.show {
  opacity: 1;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}

.hidden {
  visibility: hidden;
  opacity: 0;
  max-height: 0;
  -webkit-transition: max-height 0.3s, opacity 150ms;
  transition: max-height 0.3s, opacity 150ms;
}

.default {
  display: inline;
}

.edit {
  display: none;
}

.active {
  pointer-events: none;
  background-color: #FFFFFF;
}

.active .sidebarlink {
  color: #57068C;
}

.sidebaritem {
  padding: 10px;
}

.sidebarlink {
  color: #FFFFFF;
  font-weight: 200;
}

.userlink:hover {
  text-decoration: none;
  color: #FFFFFF;
}

.submenu {
  padding: 3px;
}

.sidebaritem:hover,
.submenu:hover {
  background-color: #FFFFFF;
}

.sidebaritem:hover .sidebarlink,
.submenu:hover .sidebarlink {
  text-decoration: none;
  color: #57068C;
}

.sidebarlink.active {
  background-color: #57068C;
}
