.lineDiv {
	position: absolute;
	width: 100%;
	height: 50%;
	margin-top: 2%;
}

.flickity-button {
	background: transparent;
}

.Timeline .flickity-viewport {
	position: absolute;
	height: 100%;
	width: 98%;
}

.Timeline .carousel-cell {
    width: 134px; /* full width */
	height: 296px;
	top: 45px;
    margin-right: 10px;
}

.line {
    position: absolute;
	display: block;
	height: 4px;
	width: 70%;
	top: 140px;
	right: 4%;
	background: #9d79bc;
}
.line::before {
    content: "";
	position: absolute;
	top: -8px;
	display: block;
	width: 0;
	height: 0;
    border-radius: 50%;
	border: 10px solid #9d79bc;
}

.line::after {
	content: "";
	position: absolute;
	top: -8px;
	display: block;
	width: 0;
	height: 0;
  border-radius: 10px;
	border: 10px solid #9d79bc;
}
.line::before {
	left: -5px;
}
.line::after {
	right: -10px;
	border: 10px solid transparent;
	border-right: 0;
	border-left: 20px solid #9d79bc;
    border-radius: 3px;
}

/* ---- Timeline elements ---- */
.Timeline {
	position: absolute;
	width: 68%;
	height: 200px;
	top: 20px;
	right: 5%;
}

.timenode {
	position: absolute;
	height: 10px;
	left: 40px;
	width: 150px;
	-webkit-transform: rotate(-45deg);
	        transform: rotate(-45deg);
	font: bold 14px arial;
	z-index: 20;
}

.timenode::before {
	content: "";
	position: absolute;
	top: 3px;
	left: -29px;
	display: block;
	width: 6px;
	height: 6px;
	border: 8px solid #9d79bc;
	border-radius: 50%;
    background: #eee;
}

/* ---- Details ---- */

.details {
	display: none;
	position: absolute;
	left: -85px;
	top: 60px;
	padding: 15px;
	border-radius: 3px;
	border-right: 2px solid rgba(126,31,134,.3);
	border-bottom: 2px solid rgba(126,31,134,.3);
	-webkit-transform: rotate(45deg);
	        transform: rotate(45deg);
	font: 12px arial;
	background: #fff;
}
.details::before {
	content: "";
	position: absolute;
	left: 10px;
	top: -9px;
	display: block;
	width: 0;
	height: 0;
	border: 10px solid transparent;
	border-bottom-color: #fff;
	border-top: 0;
}

/* ---- Hover effects ---- */

.timenode:hover {
    cursor: pointer;
    font-size: 12px;
    color: #7e1f82;
    -webkit-transition: font-size .3s;
    transition: font-size .3s;
}
.timenode:hover::before {
    border-width: 6px;
    border-color: #7e1f82;
    -webkit-transition: border-width .3s;
    transition: border-width .3s;
}
.timenode:hover .details {
	display: block;
    color: #444;
}

